<template>
  <div id="large">
    <!-- <input @click="musicControl"> -->
    <div id="button" @click="musicControl">
      <img id="icon" src="./assets/music.png" />
    </div>
    <!-- </input> -->
    <router-view></router-view>
  </div>
</template>


<script>
/* esint-disable */
import sounds from "@/assets/deer.mp3";
import { Howl, Howler } from "howler";
export default {
  data: function () {
    return {
      sound: {},
    };
  },
  mounted() {
    console.log(sounds);
    this.sound = new Howl({
      src: [sounds],
      autoplay: true,
      loop: true,
      volume: 0.6,
    });
  },
  methods: {
    musicControl() {
      if (this.sound.playing()) {
        this.sound.pause();
      } else {
        this.sound.play();
      }
    },
  },
};
</script>

<style scoped>
html,
body {
  height: 100%;
}

#large {
  height: 100%;
}

#button {
  position: absolute;
  bottom: 0px;
  background: 0;
  border: 0;
  margin: 10px;
  /* height: 50px; */
}

#icon {
  position: absolute;
  bottom: 0px;
  background: 0;
  border: 0;
  opacity: 0.6;
  height: 80px;
  filter: invert(100%);
  transition: 0.5s;
  border: 1px solid rgb(0, 0, 0);
  border-radius: 100%;
}

#icon:hover {
  opacity: 1;
}
</style>


